import React from 'react'
import styled from 'styled-components';
import sprite from "svg/symbol-defs.svg";
import Line from 'components/Line/Line';

import content from "data/about.json";


const ValuesContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(148px, 1fr));
  gap: 24px;
  width: 100%;
  margin: 36px auto 0;

  @media (min-width: 768px) {
    margin-top: 100px;
    grid-template-columns: repeat(4, 1fr);
  }

  @media (min-width: 1280px) {
    grid-template-columns: repeat(4, minmax( 274px, 1fr));
    margin-top: 124px;
    gap: 48px;
  }
`;

const ValueItem = styled.li`
  background-color: var(--accent-background);
  padding: 13px 12px 12px;
  height: 172px;
  text-align: center;
    @media (min-width: 1280px){
  height: 243px;
  padding: 48px 24px;
 }
`;

const ValueTitle = styled.h3`
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; 
  text-transform: uppercase;
   @media (min-width: 1280px){
     font-size: 32px;
  line-height: 48px;
   }
`;

const ValueDescription = styled.p`
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  text-align: justify;
  letter-spacing: -0.56px;
  margin: 12px 0 0 0;
   @media (min-width: 1280px){
    margin-top: 24px;
      font-size: 16px;
   }
`;

const StyledSvg = styled.svg`
  stroke: var(--main-color);
  fill: transparent;
  width: 16px;
  height: 16px;
  @media (min-width:1280px){
    width: 24px;
    height: 24px;
  }
`;

const Container = styled.div`
  display: flex;
  gap: 8px;
  margin-bottom: 33px;
  align-items: center;
   @media (min-width: 1280px){
    margin-bottom: 94px;
    }
`;

const ImageItem = styled.img`
display: none;
@media (min-width: 768px){
    grid-column: span 2;
 display: block;
  height: 100%; 
}
 @media (min-width: 1280px){
margin: 0 auto;
 }
`;

const Values = () => {
  console.log(content.values[3].mediumImage1x);
  return (
    <ValuesContainer>
      {content.values.map((value, index) => (


        value.type === 'image' ? (

          <li key={index} >
            <picture >

              <source
                srcSet={`
    ${value.largeImage1x} 1x, 
    ${value.largeImage2x} 2x
  `}
                media="(min-width: 1280px)"
              />
              <source
                srcSet={`
    ${value.mediumImage1x} 1x, 
    ${value.mediumImage2x} 2x
  `}
                media="(min-width: 768px)"
              />

              <ImageItem src={value.mediumImage1x} alt={value.alt} />
            </picture>
          </li>
        ) : (
          <ValueItem key={index}>
            <Container>
              <StyledSvg >
                <use href={`${sprite}#${value.svg}`} />
              </StyledSvg>
              <ValueTitle>{value.title}</ValueTitle>
            </Container>
            <Line />
            <ValueDescription>{value.description}</ValueDescription>
          </ValueItem>
        )
      ))}
    </ValuesContainer >
  );
}

export default Values;
